<template>
<svg version="1.1" @click="onTap" xmlns="http://www.w3.org/2000/svg" :viewBox="computeViewBox" :style="svgStyle">
   <g v-if="id === 'top'">
        <g :transform="`translate(8.500000, 7.000000) translate(-8.500000, -7.000000)`" :fill="this.color" fill-rule="nonzero">
            <polygon points="8.5 3.15 14.94 8.91 16.43 7.57 8.5 0.47 0.57 7.57 2.06 8.91"></polygon>
            <polygon points="0.57 12.19 2.06 13.53 8.5 7.77 14.94 13.53 16.43 12.19 8.5 5.09"></polygon>
        </g>
  </g>
  <path id="Path" class="st0" :d="computePath" v-else-if="id === 'collapsed' || id === 'expanded'"/>

</svg>
</template>

<script>
export const CLICK_EVENT = 'saved';
export default {
  name: 'svgIcon',
  beforeMount() {
    this.operator = this.color;
  },
  beforeUpdate() {
    this.operator = new Date();
  },
  props: {
    style: {
      default: '',
      type: String
    },
    color: {
      default: 'black',
      type: String
    },
    id: {
      default: 'top',
      type: String,
      required: true
    },
    size: {
      default: 16,
      type: Number
    },
    padding: {
      default: 0,
      type: Number
    },
    rotate: {
      default: 0,
      type: Number
    }
  },
  data: () => {
    return {
      colors: ['black', 'red', 'blue'],
      ids: ['top', 'collapsed', 'expanded'],
      deg: 0
    };
  },
  methods: {
    onTap() {
      // this.id = this.ids[Math.floor(Math.random()*this.ids.length)]
      // this.color = this.colors[Math.floor(Math.random()*3)];
      // this.size = [40][Math.floor(Math.random())];
      // this.operator = this.size;
      //      this.$emit(CLICK_EVENT)
    }
  },
  computed: {
    svgStyle() { return `${this.style};width:${this.size}px;box-sizing:content-box;padding:${this.padding}px;transform:rotate(${this.deg + this.rotate}deg)`; },
    computePath() {
      return (this.id == 'collapsed') ? 'M0.5,0L1,0.5H0' : 'M0.5,0.5L0,0h1';
    },
    computeViewBox() { return (this.id == 'collapsed' || this.id == 'expanded') ? '0 0 1 1' : '0 0 17 17'; }

  }
};
</script>
